import uniPopup from "@/components/uni-popup/uni-popup.vue";
export default {
  components: {
    uniPopup
  },
  data() {
    return {
      // 控制发送以及剩余时间的切换显示
      isLogin: true,
      // 定时器命名
      timer: "",
      // 倒计时内容
      num: 60,
      // 输入的手机号码
      phone: null,
      // 输入的验证码
      code: null,
      // 收到的短信验证码
      receivedCode: null
    };
  },
  methods: {
    // 点击获取验证码
    getCode() {
      // 调用定时器
      this.countDown();
      // 手机号码的正则表达式
      var reg =
        /^(((13[0-9]{1})|(15[0-9]{1})|(16[0-9]{1})|(17[3-8]{1})|(18[0-9]{1})|(19[0-9]{1})|(14[5-7]{1}))+\d{8})$/;
      if (reg.test(this.phone)) {
        this.isLogin = false;
        this.$http
          .post("/api/WeiXinApplet.ashx", {
            action: "GetCode",
            phone: this.phone
          })
          .then(res => {
            console.log(res);
            if (res.data.status == 'true') {
              uni.setStorageSync('phoneNumber', this.phone)
              this.receivedCode = res.data.code
            } else {
              uni.showToast({
                title: '今日已获取最大短信条数限制，请明日再试',
                duration: 2000,
                icon: 'none'
              });
            }
          });
      } else {
        uni.showToast({
          title: '请输入正确的手机号码',
          duration: 2000,
          icon: 'none'
        });
      }
    },
    // 验证码获取到后 点击登录
    determine() {
      if (this.code == this.receivedCode) {
        this.$http.post('/api/WeiXinApplet.ashx', {
          action: 'PhoneLogin',
          UserId: uni.getStorageSync('UserId'),
          phone: this.phone,
          code: this.code
        }).then(res => {
          console.log(res)
          if (res.data.status == 'true') {
            uni.switchTab({
              url: '/pages/me/me'
            });
          }
        })
      } else {
        uni.showToast({
          title: '您输入的验证码不正确',
          duration: 2000,
          icon: 'none'
        });
      }
    },
    // 打开遮罩层
    open() {
      this.$refs.popup.open();
    },
    // 获取用户手机号码 先判断用户登录状态是否过期 如果过期 就需要重新获取
    getPhoneNumber(e) {
      var msg = e.detail.errMsg
      var encryptedData = e.detail.encryptedData
      var session_key = uni.getStorageSync('session_key')
      var iv = e.detail.iv
      if (msg == 'getPhoneNumber:ok') {
        // 检查用户登录状态
        uni.checkSession({
          success: () => {
            // 获取手机号码
            this.deciyption(session_key, encryptedData, iv)
          },
          fail: () => {
            uni.login({
              provider: 'weixin',
              success: (res) => {
                var code = res.code
                this.$http.post('/api/WeiXinApplet.ashx', {
                  action: "Login",
                  code: code
                }).then(res => {
                  console.log(res)
                  if (res.data.status == 'true') {
                    uni.setStorageSync('openid', res.data.openid)
                    uni.setStorageSync('session_key', res.data.session_key)
                    uni.setStorageSync('UserId', res.data.UserId)
                    this.deciyption(res.data.session_key, encryptedData, iv);
                  } else {
                    uni.showToast({
                      title: '获取信息异常，请稍后重新操作！',
                      duration: 2000,
                      icon: 'none'
                    });
                  }
                })
              }
            });
          }
        })
      }
    },
    // 获取用户手机号码
    deciyption(session_key, encryptedData, iv) {
      this.$http.post('/api/WeiXinApplet.ashx', {
        action: 'GetPhoneNumber',
        session_key: session_key,
        encryptedData: encryptedData,
        iv: iv,
        UserId: uni.getStorageSync('UserId')
      }).then(res => {
        console.log(res)
        console.log(res.data.phoneNumber)
        uni.setStorageSync('phoneNumber', res.data.phoneNumber)
        if (res.data.phoneNumber) {
          uni.switchTab({
            url: '/pages/me/me'
          });
        }
      })
    },
    // 倒计时效果
    countDown() {
      // 获取倒计时的初始值
      var time = this.num;
      // 为定时器命名
      this.timer = setInterval(() => {
        // 每隔一秒 num 就减一 实现同步
        time--;
        // 然后把 num 存进 data, 让用户知道时间在倒记着
        this.num = time;
        if (time == 0) {
          clearInterval(this.timer);
          // 当时间为 0 的时候 隐藏定时器的内容 显示发送的内容 并且为定时器重新赋值
          this.isLogin = true;
          this.num = 60;
        }
      }, 1000);
    }
  }
};
